import { css, keyframes } from "emotion";

const baseStyle = css`
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1.5em;
  grid-template-areas: "col1 col2";
  div {
    animation: animate;
  }
  div:nth-of-type(1) {
    grid-area: col1;
    width: 110vh;
    div {
      margin-bottom: 10px;
      background-color: #eaeaea;
      &:nth-last-of-type() {
        margin-bottom: 0;
      }
    }
    div:nth-of-type(1) {
      height: 30px;
      width: 95%;
      margin-bottom: 1.5em;
    }
    div:nth-of-type(2),
    div:nth-of-type(3),
    div:nth-of-type(4),
    div:nth-of-type(5),
    div:nth-of-type(6),
    div:nth-of-type(7) {
      height: 13px;
      width: 100%;
    }
    div:nth-of-type(7) {
      margin-top: 1.5em;
      width: 30%;
      margin-bottom: 0;
    }
  }
  div:nth-of-type(2) {
    grid-area: col2;
    width: 100%;
    div {
      background-color: #eaeaea;
      height: 32vh;
      width: 100%;
    }
  }
  @media (max-width: 768px) {
    grid-template-columns: 1fr;
    /* grid-template-rows: 1fr 1fr; */
    grid-template-areas:
      "col2"
      "col1";
    div:nth-of-type(1) {
      width: 50vh;
    }
  }
`;

const animate = keyframes`
  0% {
    opacity: 0%;
  }

  50% {
    opacity: 30%;
  }

  70% {
    opacity: 60%
  }

  100%{
    opacity: 100%;
  }
`;

const LoadingNews = () => {
  return (
    <div className={baseStyle}>
      <div>
        <div />
        <div />
        <div />
        <div />
        <div />
        <div />
        <div />
      </div>
      <div>
        <div />
      </div>
    </div>
  );
};

export default LoadingNews;
